body {
    font-family: 'Roboto';
    max-width: 1114px;
    margin: auto;
    background-color: #c2e9fb;
}

h1 {
    text-shadow: 1px 1px 2px pink;
}

input {
    padding: 18px;
    margin: 1rem;
    border: 1px solid #828282;
    box-sizing: border-box;
    border-radius: 8px;
    width: 200px;
}

.flex-container {
    display: flex;
    flex-direction: row;
}

p {
    margin: 0;
    padding: 1rem;
}

::placeholder {
    font-weight: 500;
    font-size: 14px;
    line-height: 20px;
    color: #828282;
    padding-inline-start: 1rem;
}

label {
    display: flex;
    flex-direction: column;
}

.input:hover {
    border: 1px solid #333333;
}

.input:focus {
    border: 1px solid #2962FF;
    outline: none;
}

.error {
    border: 1px solid #D32F2F;
    outline: none;
}

.label-error {
    color: #D32F2F;
}


/* .label-error:hover {
    color: #828282;
} */

.error:hover {
    border: 1px solid #333333;
}

.error:focus {
    border: 1px solid #D32F2F;
    outline: none;
}

.error:hover+.label-error {
    color: #828282;
}

.error:focus+.label-error {
    color: #D32F2F;
}

.helpertext {
    display: flex;
    flex-direction: column;
}

.input--disabled {
    background-color: #F2F2F2;
    border: 1px solid #E0E0E0;
}

.text-error {
    color: #D32F2F;
}

.text {
    font-size: 14px;
    line-height: 20px;
    color: #333333;
}

.input--sm {
    padding-inline-start: 12px;
    padding-inline-end: 12px;
    padding-block-end: 10px;
    padding-block-start: 10px;
}

.input--md {
    padding-inline-start: 12px;
    padding-inline-end: 12px;
    padding-block-end: 18px;
    padding-block-start: 18px;
}

.fullwidth {
    width: 100%;
}

.flex-column {
    display: flex;
    flex-direction: column;
}

span {
    font-size: 10px;
    line-height: 14px;
    margin-left: 1rem;
    color: #828282;
}

img svg {
    margin: 0 0.5rem 0 0;
    width: 20px;
    height: 20px;
}